<template>
  <div>
    <Tabs value="name1">
      <TabPane label="菜单" name="name1"
        ><div class="action">
          <Input
            search
            enter-button
            placeholder="Enter the name of the recipe you want to search for"
            v-model="searchName"
            @on-search="searchData"
          />
          <div>
            <Button type="primary" class="button" @click="addModal = true"
              >newly added</Button
            >
            <Button
              type="error"
              class="button"
              :disabled="selection.length === 0"
              @click="remove"
              >delete</Button
            >
          </div>
        </div>
        <Table
          :columns="columns"
          :data="data"
          border
          stripe
          :draggable="draggable"
          :tooltip-theme="tooltipTheme"
          @on-selection-change="selectChange"
          no-data-text="The data is empty"
        >
          <template slot-scope="{ row }" slot="name">
            <span v-if="!row.editable">{{ row.name }}</span>
            <Input v-else v-model="row.name" clearable />
          </template>
          <template slot-scope="{ row }" slot="windows">
            <span v-if="!row.editable">{{ row.windows.toString() }}</span>
            <Select
              placeholder="Select"
              v-else
              v-model="row.windows"
              multiple
              :max-tag-count="4"
            >
              <Option
                v-for="(item, index) in windowsList"
                :value="item"
                :key="index"
                >{{ item }}</Option
              >
            </Select>
          </template>
          <template slot-scope="{ row }" slot="time">
            <span v-if="!row.editable">{{ row.time.join("-") }}</span>
            <TimePicker
              v-else
              v-model="row.time"
              type="timerange"
              placement="bottom-start"
              placeholder="Supply time"
            ></TimePicker>
          </template>
          <template slot-scope="{ row }" slot="type">
            <span v-if="!row.editable">{{ row.type }}</span>
            <Select placeholder="Select" v-else v-model="row.type" clearable>
              <Option
                v-for="(item, index) in typeList"
                :value="item"
                :key="index"
                >{{ item }}</Option
              >
            </Select>
          </template>
          <template slot-scope="{ row }" slot="price">
            <span v-if="!row.editable">{{ row.price }}</span>
            <InputNumber
              v-else
              :max="10000"
              v-model="row.price"
              :formatter="
                (value) => `$ ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')
              "
              :parser="(value) => value.replace(/\$\s?|(,*)/g, '')"
            ></InputNumber>
          </template>
          <template slot-scope="{ row }" slot="status">
            <span v-if="!row.editable">{{ row.status }}</span>
            <Select placeholder="Select" v-else v-model="row.status" clearable>
              <Option
                v-for="(item, index) in ['sale', 'sell out']"
                :value="item"
                :key="index"
                >{{ item }}</Option
              >
            </Select>
          </template>
          <template slot-scope="{ row }" slot="action">
            <Button
              v-show="!row.editable"
              type="primary"
              size="small"
              @click="edit(row)"
              style="margin-right: 5px"
              >modify</Button
            >
            <Button
              v-show="row.editable"
              type="success"
              size="small"
              @click="save(row)"
              style="margin-right: 5px"
              >save</Button
            >
            <Button type="error" size="small" @click="removeSingle(row)"
              >remove</Button
            >
          </template>
        </Table>
        <Modal
          v-model="addModal"
          title="New recipes"
          @on-ok="add"
          @on-cancel="cancel"
          ok-text="submit"
          cancel-text="cancel"
        >
          <Form :model="formItem" :label-width="80">
            <FormItem label="Recipes name">
              <Input v-model="formItem.name" placeholder="input recipes name" />
            </FormItem>
            <FormItem label="Supply window">
              <Select
                placeholder="Select"
                v-model="formItem.windows"
                multiple
                :max-tag-count="4"
              >
                <Option
                  v-for="(item, index) in windowsList"
                  :value="item"
                  :key="index"
                  >{{ item }}</Option
                >
              </Select>
            </FormItem>
            <FormItem label="Supply time">
              <TimePicker
                v-model="formItem.time"
                type="timerange"
                placement="bottom-start"
                placeholder="supply time"
              ></TimePicker>
            </FormItem>
            <FormItem label="Types of cuisines">
              <Select placeholder="Select" v-model="formItem.type" clearable>
                <Option
                  v-for="(item, index) in typeList"
                  :value="item"
                  :key="index"
                  >{{ item }}</Option
                >
              </Select>
            </FormItem>
            <FormItem label="Price">
              <InputNumber
                :max="10000"
                v-model="formItem.price"
                :formatter="
                  (value) => `$ ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')
                "
                :parser="(value) => value.replace(/\$\s?|(,*)/g, '')"
              ></InputNumber>
            </FormItem>
          </Form> </Modal
      ></TabPane>
      <TabPane label="地图" name="name2">
        <div id="container" style="height: 400px"></div>
      </TabPane>
    </Tabs>
  </div>
</template>
<script>
export default {
  name: "index",
  data() {
    return {
      searchName: "",
      addModal: false,
      formItem: {},
      selection: [],
      windowsList: [
        "window 1",
        "window 2",
        "window 3",
        "window 4",
        "window 5",
        "window 6",
        "window 7",
      ],
      typeList: [
        "soup",
        "meat or fish dishes",
        "vegetable dish",
        "street food",
        "staple food",
        "desserts",
      ],
      columns: [
        { type: "selection", width: 60, align: "center" },
        // { title: "预览图", key: "image", align: "center" },
        {
          title: "Recipes name",
          key: "name",
          align: "center",
          sortable: true,
          slot: "name",
          filterMethod(value, row) {
            return row.name.indexOf(value) > -1;
          },
        },
        {
          title: "Supply window",
          key: "windows",
          slot: "windows",
          align: "center",
          sortable: true,
          filterMethod(value, row) {
            return row.windows.indexOf(value) > -1;
          },
        },
        {
          title: "Supply time",
          key: "time",
          slot: "time",
          align: "center",
          sortable: true,
          filterMethod(value, row) {
            return row.time.indexOf(value) > -1;
          },
        },
        {
          title: "Types of cuisines",
          key: "type",
          slot: "type",
          align: "center",
          filterMethod(value, row) {
            return row.type.indexOf(value) > -1;
          },
        },
        {
          title: "Price",
          key: "price",
          slot: "price",
          align: "center",
          sortable: true,
          filterMethod(value, row) {
            return row.price.indexOf(value) > -1;
          },
        },
        {
          title: "Status",
          key: "status",
          slot: "status",
          align: "center",
          filterMethod(value, row) {
            return row.status.indexOf(value) > -1;
          },
        },
        { title: "Action", slot: "action", align: "center" },
      ],
      data: [],
      draggable: true,
      tooltipTheme: "dark",
      admin: 0,
      map: null,
    };
  },
  methods: {
    edit(data) {
      this.$ajax.put("api/updateDataStatus", { id: data.id }).then(() => {
        data.editable = true;
      });
    },
    save(data) {
      this.$ajax.put("api/updateData", data).then((resData) => {
        let data = resData.data;
        this.data = data;
      });
    },
    add() {
      this.$ajax.post("api/addData", this.formItem).then((resData) => {
        let data = resData.data;
        if (typeof data === "object") {
          this.data = data;
        } else {
          this.$Message.error(data);
        }
      });
    },
    remove() {
      let selection = [];
      this.selection.forEach((select) => {
        selection.push(select.id);
      });
      this.$ajax.delete("api/deleteAllData/" + selection).then((resData) => {
        let data = resData.data;
        this.data = data;
        this.selection = [];
      });
    },
    removeSingle(data) {
      this.$ajax.delete("api/deleteData/" + data.id).then((resData) => {
        let data = resData.data;
        this.data = data;
      });
    },
    get() {
      this.$ajax.get("api/getData").then((resData) => {
        this.data = resData.data;
      });
    },
    cancel() {
      this.formItem = {};
      this.addModal = false;
    },
    selectChange(selection) {
      this.selection = selection;
    },
    searchData() {
      if (this.searchName) {
        this.$ajax.get("api/getData/" + this.searchName).then((resData) => {
          this.data = resData.data;
        });
      } else {
        this.get();
      }
    },
    rowClassName({ row }) {
      if (row.status === "sell out") {
        return "demo-table-error-row";
      }
      return "";
    },
  },
  mounted() {
    let params = this.$route.params;
    if (!params.user) {
      this.$router.push("/login");
    } else {
      this.$ajax.get("api/loginIn/getAdmin/" + params.user).then((resData) => {
        let admin = resData.data + "";
        if (admin) {
          this.get();
        } else {
          this.$router.push("/login");
        }
      });
    }
    AMapLoader.load({
      key: "7de3f99e1f7190552f335470a542e1c6", // 申请好的Web端开发者Key，首次调用 load 时必填
      version: "1.4.15", // 指定要加载的 JSAPI 的版本，缺省时默认为 1.4.15
      plugins: [], // 需要使用的的插件列表，如比例尺'AMap.Scale'等
      Loca: {
        // 是否加载 Loca， 缺省不加载
        version: "1.3.2", // Loca 版本，缺省 1.3.2
      },
    })
      .then((AMap) => {
        this.map = new AMap.Map("container", {
          center: [116.397428, 39.90923],
          layers: [new AMap.TileLayer.Satellite()],
          zoom: 17,
        });
      })
      .catch((e) => {
        console.error(e); //加载错误提示
      });
  },
  destroyed() {
    this.map.destroy();
  },
};
</script>
<style lang="scss" scoped>
.action {
  display: flex;
  padding: 0 10px;
  justify-content: space-between;
  /deep/.ivu-input-wrapper {
    width: 200px;
  }
  .button {
    margin: 0 5px;
  }
}

.ivu-table .demo-table-error-row td {
  background-color: #ff6600;
  color: #fff;
}
</style>